import { generatePath } from "react-router-dom"
import { axiosClient } from "@rp/react/axios-client"
import { DocumentsCollectionAPIResponse } from "../documents.types"
import { DOCUMENTS_COLLECTION_BY_ID_API_PATH } from "../documentsAPIRoutes"

const loadDocumentsCollectionById = async (
  collectionId: string,
): Promise<DocumentsCollectionAPIResponse> => {
  const { data } = await axiosClient.get<DocumentsCollectionAPIResponse>(
    generatePath(DOCUMENTS_COLLECTION_BY_ID_API_PATH, { collectionId }),
  )

  return data
}

export { loadDocumentsCollectionById }
